import { Textarea } from './components/ui/textarea'
import { Slider } from './components/ui/slider'
import { Button } from './components/ui/button'
import { Select, SelectContent, SelectGroup, SelectItem, SelectLabel, SelectSeparator, SelectTrigger, SelectValue } from './components/ui/select'
import React, { useEffect } from 'react'
import { Input } from './components/ui/input'
import { Loader2 } from 'lucide-react'
import { Tooltip, TooltipContent, TooltipTrigger } from './components/ui/tooltip'
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from './components/ui/dialog'
import { Label } from './components/ui/label'
import { History, Undo, RefreshCcw } from "lucide-react"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from './components/ui/alert-dialog'

const default_settings = {
  "example_elephant" : `{"model_name":"cohere:command-xlarge-nightly","temperature":0.9,"maximum_length":500,"top_p":0.75,"top_k":0,"repetition_penalty":1,"num_beams":1,"num_return_sequences":1,"prompt":"Write me a short story about a elephant stuck in space"}`,
  "example_eggtart" : `{"model_name":"cohere:command-xlarge-nightly","temperature":1.0,"maximum_length":668,"top_p":0.75,"top_k":0,"repetition_penalty":1,"num_beams":1,"num_return_sequences":1,"prompt":"Please give me a recipe for Portuguese egg tarts"}`,
}

export default function App() {
  // PROBABLY USEUSE EFFECT TO CREATE A "HISTORY"
  // NEED TO ADD STOP SEQUENCES
  const [model, setModel] = React.useState<string>("cohere:medium")
  // MODEL PARAMETERS
  const [temperature, setTemperature] = React.useState<number>(0.5)
  const [maximumLength, setMaximumLength] = React.useState<number>(200)
  const [topP, setTopP] = React.useState<number>(0.75) // hugging face wants defualt of 1.0 and cohere is 0.75
  const [topK, setTopK] = React.useState<number>(50)
  const [repetitionPenalty, setRepetitionPenalty] = React.useState<number>(1.0)
  const [numBeams, setNumBeams] = React.useState<number>(1)
  const [numReturnSequences, setNumReturnSequences] = React.useState<number>(1)
  const [frequencyPenalty, setFrequencyPenalty] = React.useState<number>(0.0)
  const [presencePenalty, setPresencePenalty] = React.useState<number>(0.0)
  // TEXT AREA CONTROL
  const [prompt, setPrompt] = React.useState<string>("")
  const [output, setOutput] = React.useState<string | null>(null) // use this potentially for model output and green text?
  const [prePrompt, setPrePrompt] = React.useState<string>("")
  // LOADING STATE
  const [generating, setGenerating] = React.useState<boolean>(false)
  // PRESET CONTROL
  const [preset, setPreset] = React.useState<string | undefined>("")
  const [saveOpen, setSaveOpen] = React.useState<boolean>(false)
  const [presetName, setPresetName] = React.useState<string>("")
  const [presetList, setPresetList] = React.useState<string[]>([])
  // TEXT STREAMING
  // keep it open globally
  // const sse = new EventSource("http://0.0.0.0:5000/api/listen")
  // sse.onmessage = e => {handleStream(e)}

  function handleStream(e: MessageEvent) {
    console.log("STREAMING", e.data)
    if (e.data === "You have successfully connected.") {
      return
    }
    let new_prompt = prompt + e.data
    // console.log(new_prompt)
    setPrompt(prompt => prompt + e.data)
  }

  const handleStreamingSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    // we can open the stream here as well and then terminate it
    setGenerating(true)
    e.preventDefault()
    const sse = new EventSource("http://0.0.0.0:5000/api/listen")
    sse.onmessage = e => {handleStream(e)} // update the prompt state
    console.log("calling fetch")
    let res = null
    if (model?.startsWith("openai:")) {
      res = await fetch('http://0.0.0.0:5000/api/openai', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          repetition_penalty: repetitionPenalty,
          num_beams: numBeams,
          num_return_sequences: numReturnSequences,
          prompt: prompt
      })
      })
    } else {
      res = await fetch('http://0.0.0.0:5000/api/inference/stream', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          repetition_penalty: repetitionPenalty,
          num_beams: numBeams,
          num_return_sequences: numReturnSequences,
          prompt: prompt
      })
      })
    }
    res = await fetch('http://0.0.0.0:5000/api/inference/stream', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        model_name: model,
        temperature: temperature,
        maximum_length: maximumLength,
        top_p: topP,
        top_k: topK,
        repetition_penalty: repetitionPenalty,
        num_beams: numBeams,
        num_return_sequences: numReturnSequences,
        prompt: prompt
    })
    })
    if (res.ok) {
      console.log("STREAMING DONE")
      // close the stream
      sse.close()
      setGenerating(false)
    }
  }

  useEffect(() => {
    // Perform localStorage action, some sort of validation to only get the keys that are presets
    if (presetList.length == 0) {
      let keys = Object.keys(localStorage).filter((key_name) => {
        if (key_name.startsWith("preset_")) {
          return true
        }
        return false
      })
      keys.forEach(function(item, i) {
        keys[i] = item.replace("preset_", "")
      })
      console.log("KEYS", keys)
      setPresetList([...presetList, ...keys])
    }
  }, [])
  
  // send to local inference server
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    // TODO: change this to be cleaner
    if (!model?.startsWith("cohere:")) {
      return handleStreamingSubmit(e)
    } 
    console.log("HANDLE SUBMIT FIRED")
    setGenerating(true)
    e.preventDefault()
    let res = null
    if (model?.startsWith("cohere:")) {
      res = await fetch('http://0.0.0.0:5000/api/cohere', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          presence_penalty: presencePenalty,
          frequency_penalty: frequencyPenalty,
          prompt: prompt
      })
      })
    } else {
      res = await fetch('http://0.0.0.0:5000/api/inference', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          repetition_penalty: repetitionPenalty,
          num_beams: numBeams,
          num_return_sequences: numReturnSequences,
          prompt: prompt
      })
      })
    }
    console.log(res)
    const data = await res.json()
    if (!res.ok) {
      console.log(`Error: ${data.error}`);
      // Stop loading
      setGenerating(false);
      return;
    }
    console.log(data)
    setPrePrompt(prompt) // for undo last, just save the prompt before appending to it
    setPrompt(prompt + "\n\n" + data)
    setGenerating(false)
  }

  // for regnerating the last output
  const handleRegenerate = async (passedInPrompt: string) => {
    setGenerating(true)
    let res = null
    if (model?.startsWith("cohere:")) {
      res = await fetch('http://0.0.0.0:5000/api/cohere', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          presence_penalty: presencePenalty,
          frequency_penalty: frequencyPenalty,
          prompt: passedInPrompt
      })
      })
    } else {
      res = await fetch('http://0.0.0.0:5000/api/inference', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model_name: model,
          temperature: temperature,
          maximum_length: maximumLength,
          top_p: topP,
          top_k: topK,
          repetition_penalty: repetitionPenalty,
          num_beams: numBeams,
          num_return_sequences: numReturnSequences,
          prompt: passedInPrompt
      })
      })
    }
    const data = await res.json()
    if (!res.ok) {
      console.log(`Error: ${data.error}`);
      // Stop loading
      setGenerating(false);
      return;
    }
    console.log(data)
    setPrePrompt(passedInPrompt) // for undo last, just save the prompt before appending to it
    setPrompt(passedInPrompt + "\n\n" + data)
    setGenerating(false)
  }

  // save preset
  const handlePresetSave = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSaveOpen(false)
    const settings = JSON.stringify({
        model_name: model,
        temperature: temperature,
        maximum_length: maximumLength,
        top_p: topP,
        top_k: topK,
        repetition_penalty: repetitionPenalty,
        num_beams: numBeams,
        num_return_sequences: numReturnSequences,
        prompt: prompt
    })
    savePreset(presetName, settings)
    setPresetList([...presetList, presetName])
    setPresetName("")
  }

  // when preset selected, load it into state
  const handlePresetSelect = (value: string) => {
    const settings = JSON.parse(getPreset(value) || "{}")
    // load it into current state
    setModel(settings.model_name)
    setTemperature(settings.temperature)
    setMaximumLength(settings.maximum_length)
    setTopP(settings.top_p)
    setTopK(settings.top_k)
    setRepetitionPenalty(settings.repetition_penalty)
    setNumBeams(settings.num_beams)
    setNumReturnSequences(settings.num_return_sequences)
    setPrompt(settings.prompt)
    setPrePrompt("")
  }

  // handle preset reset to base state
  const resetToDefaultPreset = () => {
    setPreset("")
    setModel("")
    setTemperature(0.5)
    setMaximumLength(200)
    setTopP(1.0)
    setTopK(50)
    setRepetitionPenalty(1.0)
    setNumBeams(1)
    setNumReturnSequences(1)
    setPrompt("")
    setPrePrompt("")
  }

  // handle undo last
  const handleUndoLast = () => {
    setPrompt(prePrompt)
    setPrePrompt("")
  }


  // HELPER FUNCTIONS
  // Get preset from user selection, load it into state
  const getPreset = (name: string) => {
    let current_preset = ""
    if (name.startsWith("example_")) {
      current_preset = default_settings[name as keyof typeof default_settings]
    } else {
      current_preset = localStorage.getItem("preset_" + name)!
    }
    console.log(current_preset)
    return current_preset
  }

  // Save preset to local storage
  const savePreset = (name: string, data: any) => {
    localStorage.setItem("preset_" + name, data)
  }

  return (
    <>
      <div className='min-h-screen flex flex-col font-display'>
        <div className='flow mt-5 ml-5 mr-5'>
          <h1 className="scroll-m-20 text-5xl font-extrabold tracking-tight lg:text-4xl float-left">
            Playground
          </h1>
          {/* <h2 className='mt-10 scroll-m-20 border-b border-b-slate-200 pb-2 text-3xl font-semibold tracking-tight transition-colors first:mt-0 dark:border-b-slate-700'>
            RESPONSE:
            {output ? output[0] : "No response yet"}
          </h2> */}
          <div className='float-right'>
            <AlertDialog>
              <AlertDialogTrigger asChild>
                <Button variant="subtle">Click me to learn more about the demo!</Button>
              </AlertDialogTrigger>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>Thanks for checking the demo out!</AlertDialogTitle>
                  <AlertDialogDescription>
                    This is a demo of the OpenPlayground, a system for both local and remote use of LLM's with OpenAI playground functionality. Created as a part of my submission to Nat's Replit bounty.
                    <br />
                    <br />
                    <b>This demo only demonstrates remote inference. To check out local inference view my twitter post. [ADD LINK]</b>
                    <br />
                    <br />
                    <b>What works in this demo</b>
                    <ul className="mt-2 ml-6 list-disc [&>li]:mt-1">
                      <li>Inferencing of remote models (Cohere is chosen as the provider, can be extended easily to other providers)</li>
                      <li>Model selection</li>
                      <li>Generation parameter selection</li>
                      <li>Undo/Regenerate buttons</li>
                      <li>Save/Load presets</li>
                      <li>T5 models locally (view the twitter link for a demonstration of this one)</li>
                    </ul>
                    <b>What isn't included in this demo</b>
                    <ul className="mt-2 ml-6 list-disc [&>li]:mt-1">
                      <li>Keyboard shortcuts</li>
                      <li>Text streaming</li>
                      <li>OpenAI green text like injection</li>
                      <li>Stop/start/restart</li>
                    </ul>
                    <br />
                    The code is far from perfect, and there may be bugs. But I believe it's a good start. And one that is extensible to support all types of models/providers and features in the future. I'm happy to take any feedback or suggestions. You can find me on twitter at <b><a className="hover:underline" href="https://twitter.com/iamzainhuda" target="_blank">@iamzainhuda</a></b>
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogAction>Got It</AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>
          </div>
        </div>
          <div className='flow-root inline-block align-middle my-5 ml-5 mr-5 space-x-4'>
            <h4 className="scroll-m-20 text-xl font-semibold tracking-tight float-left">
             A system for both local and remote use of LLM's with OpenAI playground functionality.
            </h4>
            { /* PRESET SAVE DIALOG */ }
            <div className='float-right'>
              <Dialog open={saveOpen} onOpenChange={setSaveOpen}>
                <DialogTrigger asChild>
                  <Button variant="subtle">Save</Button>
                </DialogTrigger>
                <DialogContent className="sm:max-w-[425px]">
                  <DialogHeader>
                    <DialogTitle>Save Preset</DialogTitle>
                    <DialogDescription>
                      This will save the current playground state as a preset which you can access it later.
                    </DialogDescription>
                  </DialogHeader>
                  <form onSubmit={handlePresetSave}>
                    <div className="grid gap-4 py-2">
                      <div className="grid w-full max-w-sm items-center">
                        <Label htmlFor="name">Name</Label>
                        <Input className="mt-2 text-left" type="text" id="name" placeholder="Name" onChange={(e) => setPresetName(e.target.value)} required/>
                      </div>
                      <div className="grid w-full max-w-sm items-center gap-1.5">
                        <Label htmlFor="desc">Description (Optional)</Label>
                        <Input className="mt-2 text-left" type="text" id="desc" placeholder="A preset for..." />
                      </div>
                    </div>
                    <DialogFooter>
                      {/* <Button variant="subtle" type="reset">Cancel</Button> */}
                      <Button type="submit" value="submit">Save</Button>
                    </DialogFooter>
                  </form>
                </DialogContent>
              </Dialog>
            </div>
            { preset != "" ? 
              <div className='float-right'>
                <Button variant="subtle" onClick={resetToDefaultPreset}>Reset Preset</Button>
              </div>
            : null}
            <div className='float-right w-64'>
              <Select value={preset} onValueChange={(value) => {setPreset(value); handlePresetSelect(value)}}>
                    <SelectTrigger className="w-full">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                    <SelectGroup>
                      <SelectLabel>My Presets</SelectLabel>
                      {presetList.map((preset, i) => (
                        <SelectItem key={i} value={preset}>{preset}</SelectItem>
                      ))}
                      <SelectSeparator />
                    </SelectGroup>
                    <SelectGroup>
                      <SelectLabel>Examples</SelectLabel>
                      <SelectItem value="example_elephant">Elephant Story</SelectItem>
                      <SelectItem value="example_eggtart">Egg Tart</SelectItem>
                      <SelectSeparator />
                    </SelectGroup>
                    </SelectContent>
                </Select>
            </div>
          </div>
          { /* TEXT AREA COMPONENT */ }
          <form onSubmit={handleSubmit}>
          <div className='flex flex-row space-x-4 flex-grow ml-5 mr-5'>
              <div className="h-[70vh] flex-grow basis-5/6">
                <Textarea className='h-full w-full' placeholder="Write tagline for a ice cream shop." value={prompt} onChange={(e) => setPrompt(e.target.value)} required/>
                <div className='space-x-4'>
                  {generating ?
                  <Button className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" disabled>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Generating...
                  </Button>
                  :
                  <Button variant="default" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" type="submit" value="submit">Submit</Button> 
                  }
                  <Button variant="subtle" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" onClick={handleUndoLast} disabled={prePrompt === ""}>Undo Last</Button> 
                  <Button variant="subtle" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" onClick={e=> {e.stopPropagation(); handleUndoLast(); handleRegenerate(prePrompt)}} disabled={prePrompt === ""}>Regenerate Output</Button> 
                </div>
              </div>
            { /* SIDE BAR CHANGING VALUES */}
            <div className="grid grid-rows-8 basis-1/6 overflow-auto">
              <div className=''>
                <span className='flow-root inline-block align-middle mb-2'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Model
                    </p>  
                </span>
                <Select value={model} onValueChange={setModel} required={model === ""}>
                  <SelectTrigger className="w-full">
                    <SelectValue placeholder="Select a Model" />
                  </SelectTrigger>
                  <SelectContent>
                  <SelectGroup>
                    <SelectLabel>HUGGINGFACE</SelectLabel>
                    <SelectItem value="gpt2">gpt2</SelectItem>
                    <SelectItem value="t5-base">t5-base</SelectItem>
                    <SelectItem value="distilgpt2">distilgpt2</SelectItem>
                    <SelectItem value="prithivida/parrot_paraphraser_on_T5">prithivida/parrot_paraphraser_on_T5</SelectItem>
                    <SelectItem value="snrspeaks/t5-one-line-summary">snrspeaks/t5-one-line-summary</SelectItem>
                    <SelectItem value="mrm8488/t5-base-finetuned-question-generation-ap">mrm8488/t5-base-finetuned-question-generation-ap</SelectItem>
                    <SelectItem value="EleutherAI/gpt-neo-125M">EleutherAI/gpt-neo-125M</SelectItem>
                    <SelectSeparator />
                  </SelectGroup>
                  <SelectGroup>
                    <SelectLabel>OpenAI</SelectLabel>
                    <SelectItem value="openai:text-davinci-003">text-davinci-003</SelectItem>
                    <SelectItem value="openai:text-curie-001">text-curie-001</SelectItem>
                    <SelectItem value="openai:text-babbage-001	">text-babbage-001</SelectItem>
                    <SelectSeparator />
                  </SelectGroup>
                  <SelectGroup>
                    <SelectLabel>COHERE</SelectLabel>
                    <SelectItem value="cohere:medium">cohere:medium</SelectItem>
                    <SelectItem value="cohere:xlarge">cohere:xlarge</SelectItem>
                    <SelectItem value="cohere:command-medium-nightly">cohere:command-medium-nightly</SelectItem>
                    <SelectItem value="cohere:command-xlarge-nightly">cohere:command-xlarge-nightly</SelectItem>
                    <SelectSeparator />
                  </SelectGroup>
                  </SelectContent>
                </Select>
              </div>
              { /* TEMPERATURE */ }
              { model?.startsWith("cohere:") ? 
               <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                  <span className='flow-root inline-block align-middle mb-3'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Temperature
                    </p>  
                    <Input className='float-right text-sm' type="number" value={[temperature.toString()]} step={0.01} min={0} max={5} onChange={e => {let val = parseFloat(e.target.value); if (val > 1) { val = 1 } else if (val < 0) { val = 0 } setTemperature(val)}} />
                  </span>
                  <Slider key="temperature" defaultValue={[temperature]} min={0} max={5} step={0.01} onValueChange={e => setTemperature(e[0])} onValueCommit={e => setTemperature(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>A non-negative float that tunes the degree <br/> of randomness in generation. Lower temperatures <br/> mean less random generations. <b> Defaults to 0.5, min of 0.0 and max to 5.0</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                  <span className='flow-root inline-block align-middle mb-3'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Temperature
                    </p>  
                    <Input className='float-right text-sm' type="number" value={[temperature.toString()]} step={0.01} min={0} max={1} onChange={e => {let val = parseFloat(e.target.value); if (val > 1) { val = 1 } else if (val < 0) { val = 0 } setTemperature(val)}} />
                  </span>
                  <Slider key="temperature" defaultValue={[temperature]} min={0} max={1} step={0.01} onValueChange={e => setTemperature(e[0])} onValueCommit={e => setTemperature(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>A non-negative float that tunes the degree <br/> of randomness in generation. Lower temperatures <br/> mean less random generations. <b> Defaults to 0.5</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              }
              { /* MAX LENGTH */ }
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Maximum length
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[maximumLength.toString()]} step={1} onChange={e => setMaximumLength(parseInt(e.target.value))} min={50} max={1024} />
                    </span>
                    <Slider defaultValue={[maximumLength]} value={[maximumLength]} min={50} max={1024} step={1} onValueChange={e => setMaximumLength(e[0])} onValueCommit={e => setMaximumLength(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Maximum number of tokens to generate. <br/> Responses are not guaranted to fill up <br /> to the maximum desired length. <br/>  <b>Defaults to 200.</b> </p>
                </TooltipContent>
                </Tooltip>
              </div>
              { /* TOP P */ }
              { model?.startsWith("cohere:") ? 
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top P
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topP.toString()]} min={0} max={1} step={0.01} onChange={e => setTopP(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topP]} value={[topP]} min={0} max={1} step={0.01} onValueChange={e => setTopP(e[0])} onValueCommit={e => setTopP(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>If set to float less than 1, only the smallest <br/> set of most probable tokens with probabilities <br/> that add up to top_p or higher are kept for generation. <br /> <b>Defaults to 0.75</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top P
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topP.toString()]} min={0} max={2} step={0.01} onChange={e => setTopP(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topP]} value={[topP]} min={0} max={2} step={0.01} onValueChange={e => setTopP(e[0])} onValueCommit={e => setTopP(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>If set to float less than 1, only the smallest <br/> set of most probable tokens with probabilities <br/> that add up to top_p or higher are kept for generation. <br /> <b>Defaults to 1.0</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
      }
              { /* TOP K */ }
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top K
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topK.toString()]} min={0} max={500} step={1} onChange={e => setTopK(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topK]} value={[topK]} min={0} max={500} step={1} onValueChange={e => setTopK(e[0])} onValueCommit={e => setTopK(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>The number of highest probability vocabulary <br/> tokens to keep for top-k-filtering. <b>Defaults to 50.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              { /* FREQUENCY PENALTY */ }
              { model?.startsWith("cohere:") ? 
                <div className=''>
                  <Tooltip delayDuration={300} skipDelayDuration={150}>
                  <TooltipTrigger asChild>
                    <div>
                      <span className='flow-root inline-block align-middle mb-3'>
                        <p className="text-sm font-medium float-left align-text-top">
                          Frequency Penalty
                        </p>  
                        <Input className='float-right text-sm' type="number" value={[frequencyPenalty.toString()]} min={0} max={1} step={0.01} onChange={e => setFrequencyPenalty(parseInt(e.target.value))}/>
                      </span>
                      <Slider defaultValue={[frequencyPenalty]} value={[frequencyPenalty]} min={0} max={1} step={0.01} onValueChange={e => setFrequencyPenalty(e[0])} onValueCommit={e => setFrequencyPenalty(e[0])}/>
                    </div>
                  </TooltipTrigger>
                  <TooltipContent side='left'>
                    <p> Can be used to reduce repetitiveness of generated tokens. The higher  <br/> the value, the stronger a penalty is applied to previously present tokens, <br/> proportional to how many times they have already appeared  <br/> in the prompt or prior generation. <b>Defaults to 0.0, max value of 1.0</b></p>
                  </TooltipContent>
                  </Tooltip>
                </div>
              : null}
              { /* PRESENCE PENALTY */ }
              { model?.startsWith("cohere:") ? 
                <div className=''>
                  <Tooltip delayDuration={300} skipDelayDuration={150}>
                  <TooltipTrigger asChild>
                    <div>
                      <span className='flow-root inline-block align-middle mb-3'>
                        <p className="text-sm font-medium float-left align-text-top">
                          Presence Penalty
                        </p>  
                        <Input className='float-right text-sm' type="number" value={[presencePenalty.toString()]} min={0} max={1} step={0.01} onChange={e => setPresencePenalty(parseInt(e.target.value))}/>
                      </span>
                      <Slider defaultValue={[presencePenalty]} value={[presencePenalty]} min={0} max={1} step={0.01} onValueChange={e => setPresencePenalty(e[0])} onValueCommit={e => setPresencePenalty(e[0])}/>
                    </div>
                  </TooltipTrigger>
                  <TooltipContent side='left'>
                    <p> Can be used to reduce repetitiveness of generated tokens. Similar to <br/> frequency_penalty, except that this penalty is applied equally <br/> to all tokens that have already appeared, regardless of their <br/> exact frequencies. <b>Defaults to 0.0, max value of 1.0</b></p>
                  </TooltipContent>
                  </Tooltip>
                </div>
              : null}
              { /* Repetition penalty */ }
              { model?.startsWith("cohere:") ? null :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Repetition Penalty
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[repetitionPenalty.toString()]} min={0} max={2} step={0.01} onChange={e => setRepetitionPenalty(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[repetitionPenalty]} value={[repetitionPenalty]} min={0} max={2} step={0.01} onValueChange={e => setRepetitionPenalty(e[0])} onValueCommit={e => setRepetitionPenalty(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Akin to presence penalty. The repetition penalty is meant <br/>  to avoid sentences that repeat themselves without <br/> anything really interesting. <b>Defaults to 1.0, means no penalty.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
      }
              { /* Num Beams */ }
              { model?.startsWith("cohere:") ? null :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Number of Beams
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[numBeams.toString()]} min={0} max={10} step={1} onChange={e => setNumBeams(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[numBeams]} value={[numBeams]} min={1} max={10} step={1} onValueChange={e => setNumBeams(e[0])} onValueCommit={e => setNumBeams(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Number of beams for beam search. <br/> <b>1 means no beam search.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              }
            </div>
            </div>
        </form>
      </div>
    </>
  )
}
